<template>
  <div class="device_settings_box" id="presence_sensor_box">
    <common-header :bg="commonHeaderBg">
      <div slot="left" class="common_header_left" @click="goBackSetting">
      <!-- 返回箭头 -->
      <i class="icon iconfont">&#xe68f;</i>
      </div>
      <div slot="center" class="common_header_center oh-1">{{$t('lang.modes_usage')}}</div>
    </common-header>
    <div class="main_box">
      <div >
       <P class="text" >{{$t('lang.select_how_light_modes')}}</P>
       <p class="title_name presence_tittle_name"  v-if="oneZoneFlag==0||oneZoneFlag==1">{{ $t('lang.d_on_user_presence').toUpperCase() }}</p>
        <div class="switch_box presence_switch_box" v-if="oneZoneFlag==0||oneZoneFlag==1">
          <span class="switch_name lt-30">{{ $t('lang.presence_sensor') }}</span>
            <van-switch
              v-model="presenceSensorSwitch"
              @change="presenceSensorSwitchHandle"
              active-color="#000"
              inactive-color="#fff"
              :disabled="RemoteControlModeFlag==0 || (sabbathmode == 1 && sabbathModeStatus == 2) || onlineStatus == '2'"
              @click="toToast"
            />
        </div>
        <div :style="{opacity:presenceSensorSwitch ? 1 :0.5}" v-if="oneZoneFlag==0||oneZoneFlag==1">
          <!-- <p class="title_name">DURING USER OPENING</p> -->
          <!-- <div class="select_box">
            <span class="select_text">{{ $t('lang.d_display_change_to') }}</span>
            <p class="select_content" @click="()=>{if((sabbathmode == 0||(sabbathmode == 1 && sabbathModeStatus == 1)) && presenceSensorSwitch){presenseSensorDisplayPopup = true;};if(sabbathmode == 1 || RemoteControlModeFlag == 0 || onlineStatus == '2') {toToast()}}">{{presenseSensorDisplayText}}</p>
          </div> -->
          <div class="select_box pt-29">
            <span class="select_text fw-600">{{ $t('lang.d_light_change_to').toUpperCase() }}</span>
            <p class="select_content" @click="()=>{if((sabbathmode == 0||(sabbathmode == 1 && sabbathModeStatus == 1)) && presenceSensorSwitch){presenseSensorLightPopup = true};if(sabbathmode == 1 || RemoteControlModeFlag == 0 || onlineStatus == '2'){toToast()}}">{{presenseSensorLightText}}</p>
          </div>
        </div>

        <p class="title_name presence_tittle_name">{{ $t('lang.d_during_door_opening').toUpperCase () }}</p>
        <!-- <div class="select_box">
          <span class="select_text">DISPLAY CHANGE TO</span>
          <p class="select_content" @click="()=>openDisplayPopup = true">{{openDisplayText}}</p>
        </div> -->
        <div class="select_box">
          <span class="select_text fw-600">{{ $t('lang.d_light_change_to').toUpperCase() }}</span>
          <p class="select_content" @click="()=>{if((sabbathmode == 0||(sabbathmode == 1 && sabbathModeStatus == 1))){openLightPopup = true};if(sabbathmode == 1 || RemoteControlModeFlag == 0 || onlineStatus == '2'){toToast()}}">{{openLightText}}</p>
        </div>
        <p class="title_name presence_tittle_name">{{ $t('lang.d_after_door_closing').toUpperCase () }}</p>
        <!-- <div class="select_box">
          <span class="select_text">DISPLAY CHANGE TO</span>
          <p class="select_content" @click="()=>closeDisplayPopup = true">{{closeDisplayText}}</p>
        </div> -->
        <div class="select_box">
          <span class="select_text fw-600">{{ $t('lang.d_light_change_to').toUpperCase() }}</span>
          <p class="select_content" @click="()=>{if((sabbathmode == 0||(sabbathmode == 1 && sabbathModeStatus == 1))){closeLightPopup = true};if(sabbathmode == 1 || RemoteControlModeFlag == 0 || onlineStatus == '2'){toToast()}}">{{closeLightText}}</p>
        </div>
    </div>
  </div>
    <van-popup v-model="presenseSensorDisplayPopup" position="bottom" :style="{ height: 100 }">
      <!-- <van-picker :columns="presenseSensorDisplayList" @change="change_presenseSensorDisplay" /> -->
      <van-picker
        :columns="presenseSensorDisplayList"
        :confirm-button-text="$t('lang.confirm')"
        :cancel-button-text="$t('lang.cancel')"
        show-toolbar
        @cancel="onCancel1"
        @confirm="onConfirm1"
      />
    </van-popup>
    <van-popup v-model="presenseSensorLightPopup" position="bottom" :style="{ height: 100 }">
      <!-- <van-picker :columns="presenseSensorLightList" @change="change_presenseSensorLight" /> -->
      <van-picker
        show-toolbar
        :columns="presenseSensorLightList"
        :confirm-button-text="$t('lang.confirm')"
        :cancel-button-text="$t('lang.cancel')"
        @cancel="onCancel2"
        @confirm="onConfirm2"
      />
    </van-popup>
    <!-- open  -->
    <van-popup v-model="openDisplayPopup" position="bottom" :style="{ height: 100 }">
      <!-- <van-picker :columns="doorOpenedDisplayList" @change="change_openDisplay" /> -->
      <van-picker
        :columns="doorOpenedDisplayList"
        :confirm-button-text="$t('lang.confirm')"
        :cancel-button-text="$t('lang.cancel')"
        show-toolbar
        @cancel="onCancel3"
        @confirm="onConfirm3"
      />
    </van-popup>
    <van-popup v-model="openLightPopup" position="bottom" :style="{ height: 100 }">
      <!-- <van-picker :columns="doorOpenedLightList" @change="change_openLight" /> -->
      <van-picker
        :columns="doorOpenedLightList"
        :confirm-button-text="$t('lang.confirm')"
        :cancel-button-text="$t('lang.cancel')"
        show-toolbar
        @cancel="onCancel4"
        @confirm="onConfirm4"
      />
    </van-popup>
    <!-- close -->
    <van-popup v-model="closeDisplayPopup" position="bottom" :style="{ height: 100 }">
      <!-- <van-picker :columns="doorClosedDisplayList" @change="change_closeDisplay" /> -->
      <van-picker
        :columns="doorClosedDisplayList"
        :confirm-button-text="$t('lang.confirm')"
        :cancel-button-text="$t('lang.cancel')"
        show-toolbar
        @cancel="onCancel5"
        @confirm="onConfirm5"
      />
    </van-popup>
    <van-popup v-model="closeLightPopup" position="bottom" :style="{ height: 100 }">
      <!-- <van-picker :columns="doorClosedLightList" @change="change_closeLight" /> -->
      <van-picker
        :columns="doorClosedLightList"
        :confirm-button-text="$t('lang.confirm')"
        :cancel-button-text="$t('lang.cancel')"
        show-toolbar
        @cancel="onCancel6"
        @confirm="onConfirm6"
      />
    </van-popup>
    <common-bottom :text="$t('lang.save')" @btnHandle="saveHandle"></common-bottom>
     <loading :show-loading="showLoading"></loading>
  </div>
</template>

<script>
import baseInfo from "@/mixins/baseInfo.js";
import { Switch, Slider, Popup, DatetimePicker } from "vant";
export default {
  name: "presenceSensor",
  mixins: [baseInfo],
  data() {
    return {
      presenseSensorDisplayPopup: false,
      presenseSensorLightPopup: false,
      openDisplayPopup: false,
      openLightPopup: false,
      closeDisplayPopup: false,
      closeLightPopup: false,

      presenceSensorSwitch: true,
      presenseSensorDisplayText: "",
      presenseSensorLightText: "",
      openDisplayText: "",
      openLightText: "",
      closeDisplayText: "",
      closeLightText: "",
      clickSaveFlag: false,
      presenseSensorDisplayList: [],
      presenseSensorLightList: [],
      doorOpenedDisplayList: [],
      doorOpenedLightList: [],
      doorClosedDisplayList: [],
      doorClosedLightList: [],
      paramsInfo: {},
      RemoteControlModeFlag: 0,
      sabbathmode: 0,
      sabbathModeStatus:0,
      showLoading: false,
      onlineStatus: '1',
      oneZoneFlag:'',
      init_data:{}
    };
  },
  components: {},

  computed: {
    commonHeaderBg() {
      return {
        background: "#ffff",
      };
    },
    presenseSensorDisplayOption() {
      return this.autoGenerateText(this.presenseSensorDisplayList);
    },
    presenseSensorLightsOption() {
      return this.autoGenerateText(this.presenseSensorLightList);
    },
    doorOpenedDisplayOption() {
      return this.autoGenerateText(this.doorOpenedDisplayList);
    },
    doorOpenedLightsOption() {
      return this.autoGenerateText(this.doorOpenedLightList);
    },
    doorClosedDisplayOption() {
      return this.autoGenerateText(this.doorClosedDisplayList);
    },
    doorClosedLightsOption() {
      return this.autoGenerateText(this.doorClosedLightList);
    },
  },

  created() {
    this.oneZoneFlag = this.$route.query.oneZoneFlag || '0'; //单温区还是三温区标志位 0 三温区 1单温区(只有A温区)
     console.log("this.oneZoneFlag的值",this.oneZoneFlag);
    if(this.oneZoneFlag=='0'){
      this.presenseSensorDisplayList = [ this.$t("lang.off"), this.$t("lang.presence_clockscreen"), this.$t("lang.presence_screen") ];
      this.doorOpenedDisplayList = [ this.$t("lang.off"), this.$t("lang.presence_clockscreen"), this.$t("lang.presence_screen") ];
      this.doorClosedDisplayList = [ this.$t("lang.off"), this.$t("lang.presence_clockscreen"), this.$t("lang.presence_screen") ];
      this.doorOpenedLightList = [ this.$t("lang.off"), this.$t("lang.presence_ambient"), this.$t("lang.presence_function") ];
      this.doorClosedLightList = [ this.$t("lang.off"), this.$t("lang.presence_ambient"), this.$t("lang.presence_function") ];
      this.presenseSensorLightList = [ this.$t("lang.off"), this.$t("lang.presence_ambient"), this.$t("lang.presence_function") ];
    } else if(this.oneZoneFlag=='3'){
         this.doorOpenedLightList = [ this.$t("lang.off"), this.$t("lang.presence_ambient"), this.$t("lang.presence_function") ];
         this.doorClosedLightList = [ this.$t("lang.off"), this.$t("lang.presence_ambient"), this.$t("lang.presence_function") ];
         this.doorOpenedDisplayList = [ this.$t("lang.presence_screen"), this.$t("lang.presence_clockscreen"), this.$t("lang.off") ];
         this.doorClosedDisplayList = [ this.$t("lang.presence_screen"), this.$t("lang.presence_clockscreen"), this.$t("lang.off") ];
    } else {
      this.presenseSensorDisplayList = [this.$t("lang.off"), this.$t("lang.presence_screen")];
      this.doorOpenedDisplayList = [ this.$t("lang.off"), this.$t("lang.presence_screen")];
      this.doorClosedDisplayList = [ this.$t("lang.off"), this.$t("lang.presence_screen")];
      this.doorOpenedLightList = [ this.$t("lang.off"), this.$t("lang.presence_ambient"), this.$t("lang.presence_function") ];
      this.doorClosedLightList = [ this.$t("lang.off"), this.$t("lang.presence_ambient"), this.$t("lang.presence_function") ];
      this.presenseSensorLightList = [ this.$t("lang.presence_ambient"), this.$t("lang.presence_function") ];
    }
    this.onlineStatus = this.$route.query.onlineStatus ? this.$route.query.onlineStatus : "1";
    window.getDevicePropertiesInfoCallback = this.getDevicePropertiesInfoCallback;
    window.sendCmdCallback = this.sendCmdCallback;
    window.onBackPressedCallback = this.onBackPressedCallback;
    this.getDevicePropertiesInfo();
    this.showLoading = true
  },
  methods: {
    autoGenerateText(parameter){
    var compareList = []
      for (var key in parameter) {
          var list = {};
          list[key] = parameter[key];
          compareList.push(list)
      }
      return compareList;
    },
    // getPresenceInitDate() {
    //   this.RemoteControlModeFlag = this.$route.query.RemoteControlModeFlag || 0;
    //   this.presenceData = JSON.parse(this.$route.query.presenceData);
    //   console.log("this.presenceData", this.presenceData);
    //   this.presenseSensorDisplay = this.presenceData.PresenseSensorDisplay;
    //   this.presenseSensorLights = this.presenceData.PresenseSensorLights;
    //   this.doorOpenedDisplay = this.presenceData.DoorOpenedDisplay;
    //   this.doorOpenedLights = this.presenceData.DoorOpenedLights;
    //   this.doorClosedDisplay = this.presenceData.DoorClosedDisplay;
    //   this.doorClosedLights = this.presenceData.DoorClosedLights;
    //   this.presenceSensorSwitch =
    //     this.presenceData.PresenceSensorOnOff == 1 ? true : false;
    //   this.getPresenceDisplayText(this.presenseSensorDisplay);
    //   this.getPresenceLightText(this.presenseSensorLights);
    //   this.getOpenDisplayText(this.doorOpenedDisplay);
    //   this.getOpenLightText(this.doorOpenedLights);
    //   this.getCloseDisplayText(this.doorClosedDisplay);
    //   this.getCloseLightText(this.doorClosedLights);
    // },
    //获取设备属性列表
    getDevicePropertiesInfo() {
      let auidInfo = { deviceId: this.deviceId };
      // this.showLoading = true;
      this.$native.getDevicePropertiesInfo(
        this.from,
        this.nativeKey,
        JSON.stringify(auidInfo)
      );
      if (this.from == "html") {
        let res = {
          resultCode: 0,
          data: {
            deviceNickName: "wineCooller1wineCooller2",
            deviceProps: {
              PresenceSensorOnOff: "0",
              PresenseSensorDisplay: "0",//人感显示
              PresenseSensorLights: "1",//人感灯光

              DoorOpenedDisplay: "1",//开门显示
              DoorOpenedLights: "1",//开门灯光

              DoorClosedDisplay:"0",//关门显示状态
              DoorClosedLights:"1",//关门灯光状态
              RemoteControlMode:'1',
              SabbathMode: '0',
              SabbathModeStatus:'3'
            }
          },
          errorInfo: ""
        };
        this.getDevicePropertiesInfoCallback(res);
      }
    },
  getDevicePropertiesInfo2() {
      let auidInfo = { deviceId: this.deviceId };
      // this.showLoading = true;
      this.$native.getDevicePropertiesInfo(
        this.from,
        this.nativeKey,
        JSON.stringify(auidInfo)
      );
      if (this.from == "html") {
        let res = {
          resultCode: 0,
          data: {
            deviceNickName: "wineCooller1wineCooller2",
            deviceProps: {
              PresenceSensorOnOff: "0",
              PresenseSensorDisplay: "0",//人感显示
              PresenseSensorLights: "1",//人感灯光

              DoorOpenedDisplay: "1",//开门显示
              DoorOpenedLights: "0",//开门灯光

              DoorClosedDisplay:"0",//关门显示状态
              DoorClosedLights:"1",//关门灯光状态
              RemoteControlMode:'1',
              SabbathMode: '0',
              SabbathModeStatus:'3'
            }
          },
          errorInfo: ""
        };
        this.getDevicePropertiesInfoCallback(res);
      }
    },
      //获取设备属性列表回调
    getDevicePropertiesInfoCallback(res) {
      console.log("获取设备属性列表回调的callBack",JSON.stringify(res))

      this.proList = res;

      if (this.proList.resultCode == "0" || this.proList.resultCode == 0) {
        if ("data" in this.proList && "deviceProps" in this.proList.data) {
          let { deviceProps, deviceNickName } = this.proList.data;
          if(JSON.stringify(this.init_data) == "{}"){
            console.log('modesUsage {}')
            this.presenseSensorDisplay = Number(deviceProps.PresenseSensorDisplay);
            this.presenseSensorLights = Number(deviceProps.PresenseSensorLights);
            this.doorOpenedDisplay = Number(deviceProps.DoorOpenedDisplay);
            this.doorOpenedLights = Number(deviceProps.DoorOpenedLights);
            this.doorClosedDisplay  = Number(deviceProps.DoorClosedDisplay);
            this.doorClosedLights = Number(deviceProps.DoorClosedLights);
            this.presenceSensorSwitch =
            Number(deviceProps.PresenceSensorOnOff) == 1 ? true : false;
            if (this.oneZoneFlag == 1 || this.oneZoneFlag ==2) {
              this.getPresenceDisplayText(this.presenseSensorDisplay == 2 ? 1 : 0);
              this.getPresenceLightText(this.presenseSensorLights == 1 ? 0 : 1);
            } else {
              this.getPresenceDisplayText(this.presenseSensorDisplay);
              this.getPresenceLightText(this.presenseSensorLights);
            }
            this.getOpenDisplayText(this.doorOpenedDisplay);
            this.getOpenLightText(this.doorOpenedLights);
            this.getCloseDisplayText(this.doorClosedDisplay);
            this.getCloseLightText(this.doorClosedLights);
            this.init_data = deviceProps;//初始化页面数据
            this.RemoteControlModeFlag = Number(deviceProps.RemoteControlMode);
            this.sabbathmode = Number(deviceProps.SabbathMode)
            this.sabbathModeStatus = Number(deviceProps.SabbathModeStatus) || 0
          } else {
            console.log('modesUsage !{}')
            if(this.init_data.PresenceSensorOnOff != deviceProps.PresenceSensorOnOff){
              this.presenceSensorSwitch = Number(deviceProps.PresenceSensorOnOff);
              this.init_data.PresenceSensorOnOff = deviceProps.PresenceSensorOnOff
            }
            if(this.init_data.DoorClosedDisplay != deviceProps.DoorClosedDisplay){
              this.doorClosedDisplay = Number(deviceProps.DoorClosedDisplay);
              this.init_data.DoorClosedDisplay = deviceProps.DoorClosedDisplay
            }
            if(this.init_data.DoorClosedLights != deviceProps.DoorClosedLights){
              this.doorClosedLights = Number(deviceProps.DoorClosedLights);
              this.init_data.DoorClosedLights = deviceProps.DoorClosedLights
            }
            if(this.init_data.DoorOpenedDisplay != deviceProps.DoorOpenedDisplay){
              this.doorOpenedDisplay = Number(deviceProps.DoorOpenedDisplay);
              this.init_data.DoorOpenedDisplay = deviceProps.DoorOpenedDisplay
            }
            if(this.init_data.DoorOpenedLights != deviceProps.DoorOpenedLights){
              this.doorOpenedLights = Number(deviceProps.DoorOpenedLights);
              this.init_data.DoorOpenedLights = deviceProps.DoorOpenedLights
            }
            if(this.init_data.PresenseSensorDisplay != deviceProps.PresenseSensorDisplay){
              this.presenseSensorDisplay = Number(deviceProps.PresenseSensorDisplay);
              this.init_data.PresenseSensorDisplay = deviceProps.PresenseSensorDisplay
            }
            if(this.init_data.PresenseSensorLights != deviceProps.PresenseSensorLights){
              this.presenseSensorLights = Number(deviceProps.PresenseSensorLights);
              this.init_data.PresenseSensorLights = deviceProps.PresenseSensorLights
            }
            if (this.oneZoneFlag == 1 || this.oneZoneFlag ==2) {
              this.getPresenceDisplayText(this.presenseSensorDisplay == 2 ? 1 : 0);
              this.getPresenceLightText(this.presenseSensorLights == 1 ? 0 : 1);
            } else {
              this.getPresenceDisplayText(this.presenseSensorDisplay);
              this.getPresenceLightText(this.presenseSensorLights);
            }
            this.getOpenDisplayText(this.doorOpenedDisplay);
            this.getOpenLightText(this.doorOpenedLights);
            this.getCloseDisplayText(this.doorClosedDisplay);
            this.getCloseLightText(this.doorClosedLights);
            this.RemoteControlModeFlag = Number(deviceProps.RemoteControlMode);
            this.sabbathmode = Number(deviceProps.SabbathMode)
            this.sabbathModeStatus = Number(deviceProps.SabbathModeStatus) || 0
          }
        }
      } else {
        this.$toast(this.$t("lang.failure"));
      }
      // setTimeout(()=>{
      //   this.getDevicePropertiesInfo2()
      // },10000)
    },
    toToast() {
      if(this.onlineStatus == "2"){
        this.$toast(this.$t("lang.appliance_offline"));
      }else if (this.RemoteControlModeFlag == 0) {
        this.$toast({message: `<div style="word-break:keep-all;word-wrap:break-word;overflow:hidden;"><span>`+this.$t("lang.turn_on_the_remote")+`</span></div>`,type: 'html' });
      } else if (this.sabbathmode == 1 && this.sabbathModeStatus ==2) {
        this.$toast({message: `<div style="word-break:keep-all;word-wrap:break-word;overflow:hidden;"><span>`+this.$t("lang.turn_off_the_sabbath")+`</span></div>`,type: 'html' });
      } else {
        console.log("不提示");
      }
    },
    //人感显示
    onConfirm1(value, index) {
      // Toast(`当前值：${value}, 当前索引：${index}`);
      console.log("当前值：value", value);
      this.presenseSensorDisplayText = value;
      this.presenseSensorDisplay = index;
      this.presenseSensorDisplayPopup = false;
    },
    onCancel1() {
      this.presenseSensorDisplayPopup = false;
      console.log("取消");
    },
    //人感灯光
    onConfirm2(value, index) {
      // Toast(`当前值：${value}, 当前索引：${index}`);
      console.log("当前值：value", value);
      console.log("当前索引index", index);
      this.presenseSensorLightText = value;
      this.presenseSensorLights = index;
      this.presenseSensorLightPopup = false;
    },
    onCancel2() {
      this.presenseSensorLightPopup = false;
      console.log("取消");
    },
    //门禁打开显示
    onConfirm3(value, index) {
      // Toast(`当前值：${value}, 当前索引：${index}`);
      console.log("当前值：value", value);
      this.openDisplayText = value;
      this.doorOpenedDisplay = index;
      this.openDisplayPopup = false;
    },
    onCancel3() {
      this.openDisplayPopup = false;
      console.log("取消");
    },
    //门禁打开灯光
    onConfirm4(value, index) {
      // Toast(`当前值：${value}, 当前索引：${index}`);
      console.log("当前值：value", value);
      this.openLightText = value;
      this.doorOpenedLights = index;
      this.openLightPopup = false;
    },
    onCancel4() {
      this.openLightPopup = false;
      console.log("取消");
    },
    //门禁关闭显示
    onConfirm5(value, index) {
      // Toast(`当前值：${value}, 当前索引：${index}`);
      console.log("当前值：value", value);
      this.closeDisplayText = value;
      this.doorClosedDisplay = index;
      this.closeDisplayPopup = false;
    },
    onCancel5() {
      this.closeDisplayPopup = false;
      console.log("取消");
    },
    //门禁关闭灯光
    onConfirm6(value, index) {
      // Toast(`当前值：${value}, 当前索引：${index}`);
      console.log("当前值：value", value);
      this.closeLightText = value;
      this.doorClosedLights = index;
      this.closeLightPopup = false;
    },
    onCancel6() {
      this.closeLightPopup = false;
      console.log("取消");
    },
    getPresenceDisplayText(index) {
      this.presenseSensorDisplayOption.map((v) => {
        for (let key in v) {
          if (key == index) {
            this.presenseSensorDisplayText = v[key];
          }
        }
      });
    },
    getPresenceLightText(index) {
      this.presenseSensorLightsOption.map((v) => {
        for (let key in v) {
          if (key == index) {
            this.presenseSensorLightText = v[key];
          }
        }
      });
    },
    getOpenDisplayText(index) {
      this.doorOpenedDisplayOption.map((v) => {
        for (let key in v) {
          if (key == index) {
            this.openDisplayText = v[key];
          }
        }
      });
    },
    getOpenLightText(index) {
      this.doorClosedLightsOption.map((v) => {
        for (let key in v) {
          if (key == index) {
            this.openLightText = v[key];
          }
        }
      });
    },
    getCloseDisplayText(index) {
      this.doorClosedDisplayOption.map((v) => {
        for (let key in v) {
          if (key == index) {
            this.closeDisplayText = v[key];
          }
        }
      });
    },
    getCloseLightText(index) {
      this.doorClosedLightsOption.map((v) => {
        for (let key in v) {
          if (key == index) {
            this.closeLightText = v[key];
          }
        }
      });
      this.showLoading = false;
    },
    presenceSensorSwitchHandle(val) {
      if(this.onlineStatus == "2"){
        this.$toast(this.$t("lang.appliance_offline"));
      }else if (this.RemoteControlModeFlag == 0) {
        this.$toast({message: `<div style="word-break:keep-all;word-wrap:break-word;overflow:hidden;"><span>`+this.$t("lang.turn_on_the_remote")+`</span></div>`,type: 'html' });
      } else if (this.sabbathmode == 1 && this.sabbathModeStatus ==2) {
        this.$toast({message: `<div style="word-break:keep-all;word-wrap:break-word;overflow:hidden;"><span>`+this.$t("lang.turn_off_the_sabbath")+`</span></div>`,type: 'html' });
      } else {
        if (val) {
          this.presenceSensorSwitch = true;
        } else {
          this.presenceSensorSwitch = false;
        }
        let PresenceSensorOnOffInfo = {
          PresenceSensorOnOff: this.presenceSensorSwitch ? 1 : 0,
        };
        console.log(
          "doorSensorSettings页面传到native端的值PresenceSensorOnOffInfo",
          JSON.stringify(PresenceSensorOnOffInfo)
        );
        this.$fireBase.gaWebReport(this.from,'move','modes_usage_proximity_sensor',PresenceSensorOnOffInfo)
        this.$native.sendCmd(
          this.from,
          this.nativeKey,
          this.deviceId,
          JSON.stringify(PresenceSensorOnOffInfo)
        );
        if (this.from == "html") {
          let info = { resultCode: 0 };
          this.sendCmdCallback(info);
        }
      }
    },
    //回调
    sendCmdCallback(data) {
      console.log("控制人感返回的结果", JSON.stringify(data));
      if (data.resultCode == "0" || data.resultCode == 0) {
        this.showLoading = false;
        this.$toast(this.$t("lang.success"));

        if (this.clickSaveFlag) {
          let _this = this;
          setTimeout( () =>{
            _this.goBackSetting();
            // _this.$router.push({
            //   path: "/newLights/newLights",
            //   query: {
            //     language: _this.$i18n.locale,
            //     from: _this.$route.query.from,
            //     deviceId: _this.$route.query.deviceId,
            //     wifiId: _this.$route.query.wifiId,
            //   },
            // });
          }, 1000);
        } else {
          console.log("不跳转");
        }
      } else {
        this.$toast(this.$t("lang.failure"));
        this.showLoading = false;
        this.getDevicePropertiesInfo();
      }
    },
    onBackPressedCallback() {
      this.goBackSetting();
    },
    goBackSetting() {
      this.$router.push({
        path: "/newLights/newLights",
        query: {
          language: this.$i18n.locale,
          from: this.$route.query.from,
          deviceId: this.$route.query.deviceId,
          wifiId: this.$route.query.wifiId,
        },
      });
    },
    //保存
    saveHandle() {
      if(this.onlineStatus == "2"){
        this.$toast(this.$t("lang.appliance_offline"));
      } else if (this.RemoteControlModeFlag == 0) {
        this.$toast({message: `<div style="word-break:keep-all;word-wrap:break-word;overflow:hidden;"><span>`+this.$t("lang.turn_on_the_remote")+`</span></div>`,type: 'html' });
      } else if (this.sabbathmode == 1 && this.sabbathModeStatus ==2) {
        this.$toast({message: `<div style="word-break:keep-all;word-wrap:break-word;overflow:hidden;"><span>`+this.$t("lang.turn_off_the_sabbath")+`</span></div>`,type: 'html' });
      } else {
        this.clickSaveFlag = true;
        this.showMainView = true;
        this.applianceNameFlag = false;
        // this.showLoading = true;
        let paramsInfo;
      if(this.oneZoneFlag=='0'){
          paramsInfo = {
            PresenseSensorDisplay: this.presenseSensorDisplay,
            PresenseSensorLights: this.presenseSensorLights,

            DoorOpenedDisplay: this.doorOpenedDisplay,
            DoorOpenedLights: this.doorOpenedLights,

            DoorClosedDisplay: this.doorClosedDisplay,
            DoorClosedLights: this.doorClosedLights,
          };
        }else if(this.oneZoneFlag=='1'){
          paramsInfo = {
            PresenseSensorLights: this.presenseSensorLights,
            DoorOpenedLights: this.doorOpenedLights,
            DoorClosedLights: this.doorClosedLights,
            DoorOpenedDisplay: this.doorOpenedDisplay,
            DoorClosedDisplay: this.doorClosedDisplay
          };
        }else if(this.oneZoneFlag=='2'){
          paramsInfo = {
            DoorOpenedLights: this.doorOpenedLights,
            DoorClosedLights: this.doorClosedLights,
            DoorOpenedDisplay: this.doorOpenedDisplay,
            DoorClosedDisplay: this.doorClosedDisplay
          };
        } else {
          paramsInfo = {
            DoorOpenedLights: this.doorOpenedLights,
            DoorClosedLights: this.doorClosedLights,
            DoorOpenedDisplay: this.doorOpenedDisplay,
            DoorClosedDisplay: this.doorClosedDisplay
          };
        }
        console.log(
          "doorSensorSettings页面传到native端的值paramsInfo",
          paramsInfo
        );
        this.$native.sendCmd(
          this.from,
          this.nativeKey,
          this.deviceId,
          JSON.stringify(paramsInfo)
        );
        if (this.from == "html") {
          let info = { resultCode: "0" };
          this.sendCmdCallback(info);
        }
      }
    },
  },
};
</script>
<style lang="less" src="../deviceSettings/public.less"></style>
<style lang='less' scoped>
#presence_sensor_box {
  padding-bottom: 100px;
  min-height: 100vh;
  .text{
    font-size: 20px;
    padding-bottom:10px
  }
}
.fw-600{
  font-weight: 600;
}
.pt-29{
  padding-top: 29px;
}
.lt-30{
  line-height: 30px;
}
</style>



